import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';

const CounterWrapper = styled.div`
  font-size: 4rem;
  font-weight: bold;
  color: #fff;
  margin-bottom: 8rem;
  .title {
    font-weight: 900;
    margin-bottom: 1rem;
  }
  p {
    opacity: 0.8;
  }
  @media screen and (max-width: 860px) {
    font-size: 3rem;
  }
`;

const Counter = ({ count }) => (
  <CounterWrapper>
    <h2 className="title">Make Your Link a Masterpiece</h2>
    <p>{count > 0 && count} Links Shortened to Date</p>
  </CounterWrapper>
);

Counter.propTypes = {
  count: PropTypes.number,
};

export default Counter;
