/* eslint-disable react/no-unused-state */
import React, { Component } from 'react';
import UrlInput from '../url_input/UrlInput';
import UrlShortened from '../url_shortened/UrlShortened';
import Counter from '../counter/Counter';

export default class Main extends Component {
  constructor() {
    super();
    this.state = {
      response: {
        originalUrl: '',
        shortUrl: '',
      },
      shortened: false,
      error: false,
      count: 0,
    };
  }

  componentDidMount() {
    this.getCount();
  }

  componentDidUpdate() {
    const { error } = this.state;
    if (error) {
      return setTimeout(() => {
        this.setState({
          error: false,
        });
      }, 2000);
    }
    this.getCount();
  }

  getCount = () => {
    fetch('/api/count')
      .then(res => res.json())
      .then(data => this.setState({ count: data.count }))
      .catch(() => console.error('Failed to fetch: Count'));
  };

  onSubmit = url => {
    fetch('/shorturl/new', {
      method: 'post',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({
        url,
      }),
    })
      .then(res => res.json())
      .then(data => {
        if (data.error) {
          return this.setState({
            shortened: false,
            error: true,
          });
        }
        this.setState({
          url: '',
          response: {
            originalUrl: data.original_url,
            shortUrl: `/${data.short_url}`,
          },
          shortened: true,
          error: false,
        });
      })
      .catch(err => console.log(err));
  };

  handleChange = e => {
    this.setState({
      url: e.target.value,
    });
  };

  render() {
    const { response, shortened, error, count } = this.state;
    return (
      <>
        <div className="main_wrapper">
          <Counter count={count} />
          <UrlInput handleSubmit={this.onSubmit} isError={error} />
          {shortened && <UrlShortened response={response} />}
        </div>
      </>
    );
  }
}
