import React, { useState } from 'react';
import PropTypes from 'prop-types';
import ClipboardJS from 'clipboard';
import styled from 'styled-components';

const Links = styled.div`
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  max-width: 600px;
  border-radius: 10px;
  font-size: 1.8rem;
  margin: 0 auto;
  color: #fff;
  margin-top: 5rem;
  padding: 4rem 2.5rem;
  min-height: 220px;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.025), 0 15px 40px rgba(0, 0, 0, 0.1);
  &:after {
    content: '';
    z-index: -1;
    position: absolute;
    width: 100%;
    height: 100%;
    opacity: 0.4;
    background-image: linear-gradient(
      -20deg,
      rgba(183, 33, 255) 0%,
      #21d4fd 100%
    );
    top: 0;
  }
  .short {
    font-weight: bold;
    font-size: 2.4rem;
    margin-bottom: 1rem;
    word-break: break-all;
  }
  .url {
    text-align: center;
  }
  .copy {
    background-color: #40c3f7;
    color: #fff;
    padding: 0.5rem 1rem;
    font-size: 1.8rem;
    border-radius: 100px;
    height: 50px;
    outline: none;
    border: none;
    cursor: pointer;
    width: 150px;
    transition: all 0.2s;
    margin-top: 2rem;
    &:hover {
      background-color: #2bb0ed;
    }
  }

  .original {
    opacity: 0.5;
  }
  .copied,
  .copied:hover {
    background-color: #51ca58;
    color: #fff;
  }
`;

// eslint-disable-next-line no-new
new ClipboardJS('.copy');

const UrlShortened = ({ response: { originalUrl, shortUrl } }) => {
  const [copied, setCopied] = useState(false);
  const handleClick = () => {
    setCopied(true);
    setTimeout(() => {
      setCopied(false);
    }, 1500);
  };

  return (
    <Links>
      <div>
        <p className="url short">{shortUrl}</p>
        <p className="url original">{originalUrl}</p>
      </div>
      <button
        type="button"
        className={`copy ${copied && 'copied'}`}
        data-clipboard-text={shortUrl}
        onClick={handleClick}
      >
        {copied ? 'Copied!' : 'Copy'}
      </button>
    </Links>
  );
};

UrlShortened.propTypes = {
  response: PropTypes.object,
  originalUrl: PropTypes.string,
  shortUrl: PropTypes.string,
};

export default UrlShortened;
